import React, { useEffect } from 'react';
import Sidebar from './style/sidebar';
import styled from 'styled-components';
import { mobileWidth, gradient2, disabledTxtColor } from './config';
import TopNavBar from './style/topnav';
import useTimezone from './hooks/tz';
import { Grid, Text } from './style';
import { moment } from './utils/time'

const Layout = ({ children, style, sidebar, background }: any) => {
    const tz = useTimezone();
    useEffect(() =>
        window.scrollTo(0, 0), [])
    return (<>
        <div>
            {sidebar && <> 
                <Sidebar />   
                <TopNavBar />
            </>}
        </div>
        <Wrapper background={background} style={style} sidebar={sidebar}>
            <div
                className="left-on-desktop"
                style={{ width: '100%' }}
            >
                {children}
            
            </div>
            <Text color={disabledTxtColor} style={{placeSelf: "end center"}} className="left-on-desktop">
             2018-{moment().year().toString()} ParkStash, All Rights Reserved
            </Text>
        </Wrapper>
    </>
    );
};

Layout.Top = ({ ref =null, children, customCols =  "auto 1fr auto", noBackground = false, maxWidth = '1500px', style={}} : any) => {
    style = { maxWidth, minWidth: '85%', ...style };
    return (
        <div ref={ref} style={{ paddingBottom: '10em', background: noBackground? '':  gradient2, width: '100%' }}>
            <Grid mobileLayout={false} customCols={customCols} style={style} margin='auto'>
                {children}
            </Grid>
        </div>
    )
};

Layout.Bottom = ({ shiftUp = true, children, maxWidth = '1500px', style = {} } : any) => {
    return (
        <Grid
            margin='auto'
            placeItems="start stretch"
            height="100%"
            style={{ marginTop: shiftUp? '-10em' : '1em', maxWidth, minWidth: "85%", ...style }}
        >
            {children}
        </Grid>
    )
};


const Wrapper = styled.div<{ background: string | any, sidebar: any }>`
${(props) => `
    padding-top: ${props.sidebar ? '4em' : 0};
    background: ${props.background ? `url(${props.background})` : '#F0F3F4'};`}
    margin: auto;
    min-height: 100vh;
    display: grid;
    place-items: start stretch;
    transition: all 0.5s ease-out;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position:  center;
    background-size: cover;
    @media(max-width: ${mobileWidth}){ 
        width: 100vw;
    }`;
export default Layout;