import React from 'react';
import { Loader, Empty } from '../style';

interface List{
    loading? : boolean;
    list : any [];
    loader? : JSX.Element | string;
    emptyText? : string;
    empty? : JSX.Element | string;
    map: any;
}
export const statefulList = ({
    loading = false, 
    list, 
    loader = <Loader.Spinner/>,
    emptyText = "No Data",
    empty = <Empty text={emptyText} color="white" style={{marginTop: 0}}/>,
    map} : List )=>{
        
    if(loading)
        return loader;
    else if(list?.length === 0)
        return empty
    
    return <>{list.map(map)}</>;
}