import React, { useContext } from 'react';
import Layout from '../../layout';
import { Grid, Placeholder, Header, Button } from '../../style';
import { XyChart } from '../../model/domain/dashboard';
import { getComponents } from '../garage/analytics/util/chart-builder';
import { useSelector } from 'react-redux';
import ScoreCards from '../../components/score-card/score-cards';
import { useWidgets } from '../../hooks/widget';
import { Link } from 'react-router-dom';
import { ComponentsContext } from '../../context/components';
import { statefulList } from '../../utils/list';

const Overview = () => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const name = useSelector((state: any) => state.user.user.shortName);
    const { charts, loading, scoreCards, scoreCardOptions, editScoreCards, removeScoreCard } = useWidgets({ eId, screen: "overview" });
    const { editMode, toggleEditMode } = useContext(ComponentsContext);
    const hideOnLoading = loading ? 'hide' : '';
    const style = { maxWidth: "1500px", minWidth: "85%" };
    return (
        <Layout sidebar>
            <Layout.Top>
                <Header
                    color="white"
                    margin="1em 0 0 2em"
                    className="capitalize"
                    width="100%"
                    bold
                    textAlign="left"
                >
                    Welcome, {name}
                </Header>
                <div></div>
                <Button
                    rounded
                    shadow
                    className='hide-mobile'
                    onClick={toggleEditMode}
                    minWidth="4.5em"
                    faIcon={`fa fa-${!editMode ? 'edit' : 'check'}`}
                />
                <ScoreCards
                    loading={loading}
                    eId={eId}
                    removeScoreCard={removeScoreCard}
                    editScoreCards={editScoreCards}
                    scoreCards={scoreCards}
                    scoreCardOptions={scoreCardOptions}
                />
            </Layout.Top>
            <Layout.Bottom>
                <Grid cols="2" >
                    <Header
                        className={hideOnLoading}
                        color="white"
                        style={{ gridColumn: "1/-1" }}
                        bold
                        width="100%"
                        textAlign="left"
                    >
                        &nbsp; Today's Garage Occupancy Overview
                                </Header>
                    {statefulList({
                        list: charts,
                        loader: <Placeholder
                            lines={7}
                            number={1}
                            style={{ minHeight: "400px", minWidth: "100%", gridColumn: '1/-1' }}
                        />,
                        loading,
                        map: ((item: XyChart) => getComponents(item, () => { }, eId, 2, false))
                    })
                    }
                    <Link
                        to="/garages"
                        className={hideOnLoading}
                        style={{ gridColumn: "1/-1", placeSelf: "start" }}
                    >
                        <Button
                            rounded
                            bold
                            padding=".5em"
                            shadow
                        >
                            See Details &nbsp; <i className="fa fa-chevron-right"></i>
                        </Button>
                    </Link>
                </Grid>
            </Layout.Bottom>
        </Layout>
    );
}
export default Overview;