import React, { useState, useContext } from 'react';
import { Grid, Switch, Button, Text } from '../../style';
import Layout from '../../layout';
import Listings from './listings';
import Analytics from './analytics';
import { ComponentsContext } from '../../context/components';
import OccupancyTable from './occupancy-table';
import { useWidgets } from "../../hooks/widget";
import ScoreCards from "../../components/score-card/score-cards";
import { useSelector } from 'react-redux';

const activePages = {
  "ANALYTICS": "ANALYTICS",
  "LISTINGS": "LISTINGS",
  "OCCUPANCY": "OCCUPANCY"
};

const Garage = () => {
  const [activePage, setActivePage] = useState(activePages.ANALYTICS);
  const { editMode, toggleEditMode } = useContext(ComponentsContext);
  const eId = useSelector((state: any) => state.user.enterprise.id);
  const [open, setOpen] = useState(true);
  const filterWidth = open ? 375 : 0;

  const { loading, scoreCards, scoreCardOptions, editScoreCards, removeScoreCard } = useWidgets({ eId, screen: "garage", resource: "garage" });
  return (
    <Layout sidebar>
      <Grid customCols="1fr auto" width="100%" gridGap="0" placeItems="start" style={{ height: "100%" }}>
        <div style={{ width: "100%", placeSelf: 'start end' }}>
          <Layout.Top style={open ? { margin: '0 0 0 7.5%', maxWidth: '100%' } : {}}>
            <Switch
              style={{ placeSelf: "stretch center", gridColumn: 2 }}
              items={Object.keys(activePages)}
              onClick={(active: string) => setActivePage(active)}
              active={activePage}
            />
            <div>
              {activePage === activePages.ANALYTICS && !open &&
                <span className='pointer bold' style={{ color: 'white' }} onClick={() => setOpen(true)}>Show Filter</span>
              }

              <Button
                rounded
                shadow
                margin='0 1em'
                className='hide-mobile'
                onClick={toggleEditMode}
                minWidth="4.5em"
                faIcon={`fa fa-${!editMode ? 'edit' : 'check'}`}
              />
            </div>
            <ScoreCards
              loading={loading}
              eId={eId}
              style={{ gridColumn: 2 }}
              removeScoreCard={removeScoreCard}
              editScoreCards={editScoreCards}
              scoreCards={scoreCards}
              scoreCardOptions={scoreCardOptions}
            />
          </Layout.Top>
          <Layout.Bottom
            style={open ?
              { margin: '-10em 0 0 7.5%', maxWidth: '100%' } :
              { margin: '-10em auto auto auto', maxWidth: '1500px' }}
          >
            {activePage === activePages.LISTINGS ? <Listings /> :
              activePage === activePages.OCCUPANCY ? <OccupancyTable />
                : <Analytics filterWidth={filterWidth} open={open} setOpen={setOpen} />
            }
          </Layout.Bottom>
        </div>
        {activePage === activePages.ANALYTICS && <div style={{ width: filterWidth }}></div>}
      </Grid>
    </Layout>
  );
}

export default Garage;
