import React, { useMemo, useState } from 'react'
import { useTooltip, TooltipWithBounds } from '@vx/tooltip';
import { localPoint } from '@vx/event';
import { AreaClosed  } from '@vx/shape';
import { ScaleTime, ScaleLinear } from 'd3-scale';
import { moment, Moment, FORMAT_DATETIME_2 } from '../../utils/time';
import { TimeSeries } from '../../model/domain/metrics';
import Line from '@vx/shape/lib/shapes/Line';
import { curveStep as curve, curveBasis } from '@vx/curve';
import { LinearGradient, GradientPinkRed } from '@vx/gradient';
import useRandom from '../../hooks/random';
import { primaryColor } from '../../config';
import { GridRows } from '@vx/grid';
import {Text, Grid } from '../../style';
import { AreaStack } from '@vx/shape';
import { GradientOrangeRed } from '@vx/gradient';
import { scaleTime, scaleLinear } from '@vx/scale';

const colors = [
  '#4184f3',/*  blue1*/
  '#ff5722',/*  redOrange*/
  '#ff8f00',/*  orange1*/
  '#7cb342',/*  green1*/
  '#aa46bb',/*  purple1*/
  '#00abc0',/*  cyanblue1*/
  '#f57c00',/*  orange2*/
  '#9d9c23',/*  green2*/
  '#5b6abf',/*  purple2*/
  '#ef6191',/*  pink1*/
  '#00786a',/*  green*/
  '#c1175a',/*  redpink1*/
  '#fa937f',/*  cream1*/
  '#0fbfff',/*  skyblue1*/
  '#e91e63',/*  redpink2*/
  '#c1175a',/*  redpink3*/
  '#31d8d0',/*  cyan2*/
];

export const accentColor = "lightgray";
export interface DrawAreaProps {
  backgroundColor: string;
  width? : number;
  height: number;
  timeSeriesResult: TimeSeries.Result,
  metricDescriptor: TimeSeries.MetricDescriptor,
  dateScale: ScaleTime<number, number>
  yScale: ScaleLinear<number, number>
}

interface TooltipData {
  timestamp: Moment;
  legend: { label: string, value: string, color: string }[];
}

export type StackedAreasProps = {
  width?: number;
  height?: number;
  events?: boolean;
  margin?: { top: number; right: number; bottom: number; left: number };
};

const reducer = (datum: TimeSeries.Point): boolean => !datum.isMissing;

export function DrawArea({ width, height, timeSeriesResult, metricDescriptor, dateScale, yScale : yScale2 }: DrawAreaProps) {
  const ttip = useTooltip<TooltipData>();
  const [ selected, setSelected ] = useState(-1);
  const getColor = (index: number) => colors[index % colors.length];

  function handleTooltip(event : any, newSelected = -1) {
    const { x, y } = localPoint(event) || {};
    setSelected(newSelected)
    const bisect = (t: Moment) => {
      let points = timeSeriesResult.timeSeries[0].points;
      for (let i = points.length - 1; i >= 0; i--) {
        const at = points[i].at;
        if (t.isSameOrBefore(at)) {
          return i;
        }
      }
      return 0;
  }

    function getLegend(i: number) {
      const timeSeries = timeSeriesResult.timeSeries;
      const toPercent = (n: number) => (n * 100).toFixed(2) + '%';
      const legend = timeSeries.reduce((acc, ts, index) => {
        const p = ts.points[i];

        if (!p.isMissing) {
          const label = timeSeriesResult.crossSeriesReducer === 'NONE'
            ? ts.displayLabel
            : metricDescriptor.displayName;

          const value = timeSeriesResult.unit === '%' ? toPercent(p.value) : p.value + '';
          const temp = { label, value, color: getColor(index) }
          acc.push(temp as never);
        }

        return acc;
      }, []);
      return legend;
    }

    const timestamp = moment(dateScale.invert(x || 0));
    const i = bisect(timestamp);
    const legend = getLegend(i);
    ttip.showTooltip({
      tooltipData: { timestamp, legend },
      tooltipLeft: x,
      tooltipTop: y,
    });
  }

  const dataPoints2: TimeSeries.Point[][] = useMemo(() => {
    const k = timeSeriesResult.alignmentPeriod;
    return timeSeriesResult.timeSeries.map(ts => {
      const pairedPoints = ts.points.reduce((pairedPoints, point) => {
        const pointPair = {
          value: point.value,
          isMissing: point.isMissing,
          at: moment(point.at).subtract(k, 'minutes')
        }
        
        pairedPoints.push(point as never);
        pairedPoints.push(pointPair  as never);
        return pairedPoints;
      }, []);

      return pairedPoints;
    });
  }
    , [timeSeriesResult]);

  const dataPoints = useMemo(() => {
      const points : any[]= [];
      const{ timeSeries } = timeSeriesResult
      const reference = timeSeries[0]
      const numPoints = reference.points.length;
      const numResource = timeSeries.length
    
      for(let i = 0; i < numPoints; i++){ 
        let point = {
          date: reference.points[i].at
        };

        for(let j = 0; j < numResource; j++){
          const resource = timeSeries[j].displayLabel;
          const value = timeSeries[j].points[i].value;
          point = {
            ...point,
            [resource] : String(value)
          }

        }
        points.push(point)
      }

      console.log(points)
      return points;
    }
      , [timeSeriesResult]);

  
  const max = useMemo(() => {
        const points : any[]= [];
        const{ timeSeries } = timeSeriesResult
        const reference = timeSeries[0]
        const numPoints = reference.points.length;
        const numResource = timeSeries.length
        let max = 0;
        for(let i = 0; i < numPoints; i++){ 
          let totalValue = 0;
          for(let j = 0; j < numResource; j++){
            totalValue += timeSeries[j].points[i].value;
          }
          max = max < totalValue? totalValue : max;
        }

        return max;
      }, [timeSeriesResult]);

  const linePathX = (d: TimeSeries.Point) =>  dateScale(moment(d.at));
  const linePathY = (d: TimeSeries.Point) =>  yScale2(d.value);
  
  const { random } = useRandom({})
  const scribble2 = timeSeriesResult.crossSeriesReducer === 'NONE'
    ? (dataPoints2.map((points, index) => {
        const color = getColor(index);
        const rand = Math.random() * 10000;
     return (
       <>
        <LinearGradient id={"area-gradient" + rand} from={color} to={'white'} toOpacity={0.7}/>
        <AreaClosed<TimeSeries.Point>
          data={points}
          x={linePathX}
          y={linePathY}
          yScale={yScale2}
          onTouchStart={handleTooltip}
          onTouchMove={handleTooltip}
          onMouseMove={handleTooltip}
          onMouseLeave={() => ttip.hideTooltip()}
          strokeWidth={2}
          defined={reducer}
          stroke={color}
          fill={"url(#area-gradient" + rand + ")"}
          curve={curveBasis}
        />
       </>
      ); }))
    : <>
      <LinearGradient id={"area-gradient" + random} from={accentColor} to={accentColor} toOpacity={0.3}/>
      <AreaClosed<TimeSeries.Point>
        data={dataPoints2[0]}
        x={linePathX}
        y={linePathY}
        yScale={yScale2}
        onTouchStart={handleTooltip}
        onTouchMove={handleTooltip}
        onMouseMove={handleTooltip}
        onMouseLeave={() => ttip.hideTooltip()}
        strokeWidth={2}
        defined={reducer}
        stroke={primaryColor}
        fill={"url(#area-gradient" + random + ")"}
        curve={curveBasis}
      />
    </>

  const data = dataPoints;
  const keys = Object.keys(data[0]).filter(k => k !== 'date') as any[];

  const getDate = (d: any) => (new Date(d.date) as Date).valueOf();
  const getY0 = (d: any) => d[0];
  const getY1 = (d: any) => d[1];

  const xScale = scaleTime<number>({
    range: [0, width || 700],
    domain: [Math.min(...data.map(getDate)), Math.max(...data.map(getDate))],
  });

  const yScale = (() => {
    return scaleLinear<number>({
      domain: [0, max * 1.25],
      range: [height ||  700, 0],
      });
  })();

  const scribble =  <>
    <GradientOrangeRed id="stacked-area-orangered" />
    <GradientPinkRed id="stacked-area-pinkred" />
    <AreaStack
    keys={keys}
    data={data}
    x={d => xScale(getDate(d.data))}
    y0={d => yScale(getY0(d))}
    y1={d => yScale(getY1(d))}
    >
    {({ stacks, path }) =>
      stacks.map((stack, index) => {
        const isActive = selected === index || selected === -1;
        const opacity = isActive? '37': '10';
        const color = getColor(index) + opacity;
    return(
        <path
          key={`stack-${stack.key}`}
          d={path(stack) || ''}
          stroke={color}
          strokeWidth={2}
          onTouchStart={(e)=>handleTooltip(e, index)}
          onTouchMove={(e)=>handleTooltip(e, index)}
          onMouseMove={(e)=>handleTooltip(e, index)}
          onMouseLeave={() => {
            setSelected(-1)
            ttip.hideTooltip()
          }} 
          fill={color}
        />
      )})
    }
  </AreaStack>
  </>
  return (
    <>
      <svg width={width} height={height}>
      <rect
          x={0}
          y={0}
          width={width}
          height={height}
          fill={'transparent'}
          onTouchStart={handleTooltip}
          onTouchMove={handleTooltip}
          onMouseMove={handleTooltip}
          onMouseLeave={() => ttip.hideTooltip()} 
        />        
        <GridRows<number>
          scale={yScale2}
          width={width || 0}
          stroke={accentColor}
          strokeOpacity={0.5}
          pointerEvents="none"
        />        
        {scribble}
        {
          ttip.tooltipData &&
          <g>
            <Line
              from={{ x: ttip.tooltipLeft, y: 0 }}
              to={{ x: ttip.tooltipLeft, y: height }}
              stroke={primaryColor}
              strokeWidth={2}
              pointerEvents="none"
              strokeDasharray="5," />
          </g>
        }
      </svg>
      {
        ttip.tooltipOpen &&
        <TooltipWithBounds 
          top={ttip.tooltipTop}
          left={ttip.tooltipLeft}>
          <TooltipContent data={ttip.tooltipData as any} />
        </TooltipWithBounds>
      }
    </>
  )
}

function TooltipContent({ data }: { data: TooltipData }) {
  return (
    <Grid placeItems="start" customRows="auto 1fr" gridGap=".4em">
      <Text bold>{data.timestamp.format(FORMAT_DATETIME_2)}</Text>
      {
        data.legend.map(({ color, label, value }, index) => (
          <div key={index} style={{ padding: 2 }}>
            <span className='round' style={{ background: color }}></span>&nbsp;
            <b>{label}:</b>&nbsp;{value}
          </div>
        ))
      }
    </Grid>
  );
}