import { LineChart } from "../model/domain/dashboard";
import { useReducer } from "react";

export const initialState : LineChart  = {
    title: "Occupancy",
    timeSeriesQuery: {
      metricType: "garage_occupancy_count",
      resourceIds: [],
      crossSeriesReducer: "NONE",
      alignment: null
    },
    backgroundColor: "#FFFFFF",
    xAxisLabel: "T",
    yAxisLabel: "Occupancy",
  };
  
  
  export enum actions {
   SET_GARAGES,
   SET_ALIGNMENT,
   SET_METRICTYPE
  }
  
  export function reducer(state: LineChart, action: any) {
    const setTimeseries = (props: any) => {
      const { timeSeriesQuery } = state;
      return {
        ...state,
        timeSeriesQuery: {
          ...timeSeriesQuery,
          ...props,
        },
      };
    };

    switch (action.type) {
      case actions.SET_METRICTYPE:
        return setTimeseries({ metricType: action.payload.metricType });

      case actions.SET_GARAGES:
        return setTimeseries({ resourceIds: action.payload.resourceIds });

      case actions.SET_ALIGNMENT:
        const alignment = action.payload?.alignment || null;
        return setTimeseries({ alignment });

      default:
        return state;
    }
  }

  const useTimeseries = ()=>{
    const [timeseries, dispatch] = useReducer( reducer, initialState);


    const setGarages = (resourceIds: string[]) => dispatch({type: actions.SET_GARAGES, payload: { resourceIds }});
        
   const setMetric = (metricType: string) => dispatch({type: actions.SET_METRICTYPE, payload: { metricType }});
   const setAlignment = (e: any) => dispatch({type: actions.SET_ALIGNMENT, payload: e})

   return {
       timeseries,
       actions : {
           setGarages,
           setAlignment,
           setMetric
       }
   }
  }

  export default useTimeseries;