import React from 'react';
import  LineChart from '../../../../components/line-chart/chartWithControl';
import StackedGraph from '../stacked-graph';
import { XyChart } from '../../../../model/domain/dashboard';

export const getComponents = (El: XyChart, remove : any, eId: string, span = 1, filter = true) =>{
    const isEditable = (El : any)=> El.editable !== false;
    switch(El.type){
        case "lineChart":   
        return  <LineChart {...El.lineChart as any} filter={filter} span={span}   />

        default:
        return<StackedGraph {...El}  eId={eId} editable={isEditable(El)} remove={remove} />
    }
}