import React, { useReducer, useEffect } from 'react';
import { Grid } from '../../../style';
import LineChart from '../../../components/line-chart/chartWithControl';
import { useMetricData } from '../../../hooks/metrics';
import Filter from './filter';
import { useSelector } from 'react-redux';
import useTimeseries from '../../../hooks/timeseries';

const Analytics = ({ filterWidth, open, setOpen } : any) => {
  const eId = useSelector(({ user }: any) => user.enterprise.id);
  const { timeseries, actions } =  useTimeseries();

  const { metricDataStatus, setStartTime, interval, setEndTime, refresh, setInterval} = useMetricData({
    timeseries: timeseries.timeSeriesQuery,
    eId
  });
  
  const props = {
    chart : timeseries,
    metricDataStatus,
    interval,
    filterWidth,
    actions: {
      ...actions,
      setStartTime,
      setEndTime,
      setInterval
    }
  }

 useEffect(()=> {refresh(timeseries)}, [timeseries])
  return (
    <Grid placeItems="start stretch" padding="1em" height="100%">
      <Grid cols="1">
            <LineChart
                className='slide-up' 
                filterWidth={ filterWidth}
                metricDataStatus={metricDataStatus}
                {...timeseries}    
              />
          <Filter {...props} open={open} toggle={()=>setOpen(false)} />
      </Grid>
    </Grid>
  );
}

export default Analytics;
